import FileCode from '@/components/Attachments/Icons/FileCode.vue';
import FileCompressed from '@/components/Attachments/Icons/FileCompressed.vue';
import FileCsv from '@/components/Attachments/Icons/FileCsv.vue';
import FileExcel from '@/components/Attachments/Icons/FileExcel.vue';
import FileGeneric from '@/components/Attachments/Icons/FileGeneric.vue';
import FileImage from '@/components/Attachments/Icons/FileImage.vue';
import FilePdf from '@/components/Attachments/Icons/FilePDF.vue';
import FilePowerPoint from '@/components/Attachments/Icons/FilePowerPoint.vue';
import FilePresentation from '@/components/Attachments/Icons/FilePresentation.vue';
import FileSound from '@/components/Attachments/Icons/FileSound.vue';
import FileSpreedSheet from '@/components/Attachments/Icons/FileSpreasheet.vue';
import FileText from '@/components/Attachments/Icons/FileText.vue';
import FileVideo from '@/components/Attachments/Icons/FileVideo.vue';
import FileWord from '@/components/Attachments/Icons/FileWord.vue';

import { computed } from 'vue';
import { Color } from '@/enums';

export function useAttachmentsIcon() {
  const types = [
    {
      extension: [
        'html',
        'htm',
        'css',
        'js',
        'ts',
        'json',
        'xml',
        'py',
        'java',
        'c',
        'cs',
        'rb',
        'php',
      ],
      icon: FileCode,
    },
    { extension: ['zip', 'rar', '7z', 'tar', 'tar.gz'], icon: FileCompressed },
    { extension: ['csv', 'tsv'], icon: FileCsv },
    { extension: ['xls', 'xlsx'], icon: FileExcel },
    {
      extension: ['jpg', 'jpeg', 'png', 'gif', 'svg', 'webp'],
      icon: FileImage,
    },
    { extension: ['pdf'], icon: FilePdf },
    { extension: ['ppt', 'pptx', 'pot', 'potx'], icon: FilePowerPoint },
    { extension: ['odp'], icon: FilePresentation },
    { extension: ['mp3', 'wav', 'ogg', 'm4a'], icon: FileSound },
    { extension: ['ods'], icon: FileSpreedSheet },
    { extension: ['txt', 'odt', 'rtf', 'md'], icon: FileText },
    { extension: ['mp4', 'avi', 'mov', 'mkv', 'm4v'], icon: FileVideo },
    { extension: ['doc', 'docx', 'dot', 'dotx'], icon: FileWord },
  ];

  const findIconByName = computed(() => {
    return (filename: string) => {
      const arr = filename.split('.');
      const extension = arr.pop();

      if (!extension) return FileGeneric;
      const index = types.findIndex((type) =>
        type.extension.includes(extension)
      );

      return index !== -1 ? types[index].icon : FileGeneric;
    };
  });

  const color = computed(() => {
    return (progress: number | undefined) => {
      if (progress && progress < 100) return Color.TextSecondary;

      return Color.TextPrimary;
    };
  });

  return {
    findIconByName,
    color,
  };
}
