<template>
  <div
    class="attachments-item"
    @mouseover="actionVisibleIsTruthy"
    @mouseleave="actionVisibleIsFalsy"
  >
    <div
      class="attachments-description"
      :class="{
        'upload-progress': file.progress < 100,
      }"
    >
      <Component
        :is="findIconByName(file.name)"
        :progress="file.progress"
      />
      <span class="attachments-description-text">
        {{ file.name }}
      </span>
    </div>
    <div
      v-if="actionsIsVisible || file.progress < 100"
      class="attachments-action"
    >
      <template v-if="file.progress === 100">
        <button
          class="attachments-button"
          @click="emit('remove-item', file.id)"
        >
          <svg
            width="16"
            height="17"
            viewBox="0 0 16 17"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M5.2002 14.1C4.8702 14.1 4.5877 13.9825 4.3527 13.7475C4.1177 13.5125 4.0002 13.23 4.0002 12.9V4.89999H3.2002V3.69999H6.4002V2.89999H9.6002V3.69999H12.8002V4.89999H12.0002V12.8928C12.0002 13.2309 11.8827 13.5167 11.6477 13.75C11.4127 13.9833 11.1302 14.1 10.8002 14.1H5.2002ZM10.8002 4.89999H5.2002V12.9H10.8002V4.89999ZM6.4002 11.7H7.6002V6.09999H6.4002V11.7ZM8.4002 11.7H9.6002V6.09999H8.4002V11.7Z"
              fill="#0676C5"
            />
          </svg>
        </button>
        <UploadFailed
          v-if="!file.success && file.message"
          :title="file.message"
          :index="index"
          :file="file"
          @try-again="(data) => emit('try-again', data)"
        />
      </template>
      <AttachmentProgress
        v-if="file.progress < 100"
        :progress="file.progress"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, watchEffect } from 'vue';
import type { FileUploadData } from '@/types';
import UploadFailed from '@/components/Attachments/UploadFailed.vue';
import AttachmentProgress from '@/components/Attachments/AttachmentProgress.vue';
import { useAttachmentsIcon } from '@/composables';

interface Props {
  file: FileUploadData;
  index: number;
}

interface Emit {
  (e: 'remove-item', id: string): void;
  (e: 'upload-finished', id: string): void;
  (e: 'try-again', index: number): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emit>();

const { findIconByName } = useAttachmentsIcon();

const actionsIsVisible = ref(false);

function actionVisibleIsTruthy() {
  actionsIsVisible.value = true;
}

function actionVisibleIsFalsy() {
  actionsIsVisible.value = false;
}

watchEffect(() => {
  if (props.file.progress === 100) {
    emit('upload-finished', props.file.id);
  }
});
</script>

