<template>
  <div class="attachments-container">
    <PerfectScrollbar ref="scrollbar">
      <div class="attachments-list">
        <AttachmentsItem
          v-for="(fileData, index) in filesData"
          :key="fileData.id"
          :index="index"
          :file="fileData"
          @remove-item="removeItem"
          @upload-finished="scrollUpdate"
          @try-again="(data) => emit('try-again', data)"
        />
      </div>
    </PerfectScrollbar>
  </div>
</template>

<script setup lang="ts">
import type { FileUploadData } from '@/types';
import AttachmentsItem from '@/components/Attachments/AttachmentsItem.vue';
import { ref, watch } from 'vue';

interface Props {
  filesData: FileUploadData[];
}

interface PScrollBar {
  $el: Element;
  ps: {
    contentHeight: number;
    element: Element;
  };
}

interface Emit {
  (e: 'remove-item', id: string): void;
  (e: 'try-again', index: number): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emit>();

const scrollbar = ref<PScrollBar>();

function removeItem(id: string) {
  emit('remove-item', id);
}

function scrollUpdate(id: string) {
  const index = props.filesData.findIndex((file) => file.id === id);

  if (index !== -1) {
    (scrollbar.value as PScrollBar).$el.scrollTop = 28 * (index + 1);
  }
}

watch(
  () => props.filesData,
  (newFiles) => {
    const fileData = newFiles.find((file) => file.progress < 100);
    const index = props.filesData.findIndex((file) => file.id === fileData?.id);

    if (index !== -1) {
      (scrollbar.value as PScrollBar).$el.scrollTop = 28 * (index + 1);
    }
  },
  { deep: true }
);
</script>

<style scoped>
.attachments-container {
  border-left: 1px solid #e9ecef;
}

.attachments-list {
  height: 98px;
  width: 255px;
  padding: 4px;
  display: flex;
  flex-direction: column;
  gap: 2px;
}
</style>
