<template>
  <button
    v-bind="$attrs"
    class="base-format-button"
    :class="{ active: editor.isActive(formatName) }"
    :disabled="disabled"
  >
    <component :is="icon" />
  </button>
</template>

<script lang="ts">
export default {
  inheritAttrs: false,
};
</script>

<script setup lang="ts">
import { useRoute } from 'vue-router';
import { computed } from 'vue';
import type { Component } from 'vue';
import { RouteName } from '@/enums';
import type { Editor } from '@tiptap/vue-3';

interface Props {
  icon: Component;
  formatName: string;
  editor: Editor;
}

defineProps<Props>();
const route = useRoute();

const disabled = computed(
  () =>
    route.name === RouteName.Sms ||
    route.name === RouteName.FavoriteContact ||
    route.name === RouteName.Contact
);
</script>

<style scoped>
.base-format-button {
  all: unset;
  cursor: pointer;
  background-color: transparent;
  border-radius: 4px;
  width: 28px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.base-format-button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.base-format-button:disabled path {
  opacity: 0.3;
}

.base-format-button:hover:not(:disabled),
.base-format-button.active {
  background-color: #e2e8f0;
}

.base-format-button:hover:not(:disabled) svg path,
.base-format-button.active svg path {
  fill: #344767;
}
</style>
