<template>
  <div style="z-index: 3">
    <button v-bind="api.triggerProps" class="to-sms">
      <span>
        {{ t('To') }}
      </span>
      <span class="to-number">
        <span>{{ phoneMasked(numberSelected as string) }}</span>
      </span>
    </button>
    <div v-if="api.isOpen && numbersTo.length > 1" v-bind="api.positionerProps">
      <div v-bind="api.arrowProps">
        <div v-bind="api.arrowTipProps" />
      </div>
      <ul v-bind="api.contentProps" ref="contentRef" class="to-sms-numbers">
        <li
          v-for="number in numbersTo"
          :key="number"
          v-bind="api.getItemProps({ id: number })"
        >
          <div class="to-sms-number">
            <ICheck
              v-if="numberSelected === number"
              size="16"
              class="to-sms-check"
            />
            <span>{{ phoneMasked(number) }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup lang="ts">
import * as menu from '@zag-js/menu';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, ref, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import ICheck from '@/components/Icons/ICheck.vue';
import { phoneMasked } from '@/utils';
import { useRoute, useRouter } from 'vue-router';
import { useChat } from '@/composables';
import { RouteName } from '@/enums';

interface Emit {
  (e: 'dst-number', value: string): void;
}

const emit = defineEmits<Emit>();

const { t } = useI18n();
const { numbersTo } = useChat();
const route = useRoute();
const router = useRouter();

const numberSelected = ref(numbersTo.value[0]);
const contentRef = ref<HTMLDivElement>();

const topArrow = computed(() => {
  if (contentRef.value) {
    return `${contentRef.value.offsetHeight - 8}px`;
  }
  return '99px';
});

const [state, send] = useMachine(
  (menu as any).machine({
    id: 'to-sms',
    positioning: { placement: 'top-start', offset: { crossAxis: 52 } },
    onSelect(details: { value: string }) {
      numberSelected.value = details.value;
      emit('dst-number', numberSelected.value);
    },
  })
);
const api = computed(() =>
  menu.connect(state.value as any, send, normalizeProps)
);

function findIndexNumbersByNumber(number: string) {
  return numbersTo ? numbersTo.value.findIndex((n) => n === number) : -1;
}

watchEffect(() => {
  const routeName = String(route.name);
  const number = route.query.number ? String(route.query.number) : '';
  switch (routeName) {
    case RouteName.SendSms:
    case RouteName.FavoritePhone: {
      numberSelected.value = String(route.params.id);
      break;
    }
    case RouteName.Directory:
    case RouteName.FavoriteDirectory: {
      const index = findIndexNumbersByNumber(number);
      if (!number) break;
      else if (index !== -1) {
        numberSelected.value = numbersTo.value[index];
      } else {
        numberSelected.value = numbersTo.value[0];
      }
      break;
    }
    case RouteName.Sms: {
      numberSelected.value = route.query.number
        ? String(route.query.number)
        : '';
      break;
    }
    default: {
      numberSelected.value = String(route.params.id);
    }
  }
  emit('dst-number', numberSelected.value);
  if (route.name !== RouteName.Sms) {
    const query = route.query;
    delete route.query.number;
    router.replace({ path: route.path, query });
  }
});
</script>

