  all: unset;
  cursor: pointer;
  height: 100%;
  color: var(--text-secondary);
  font-size: 14px;
  padding: 0 8px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.to-sms-numbers {
  background-color: var(--white-500);
  min-width: 248px;
  border-radius: 8px;
  border: 1px solid var(--line);
  list-style: none;
  padding: 0;
  max-height: 107px;
  overflow-y: auto;
}

.to-sms-number {
  padding-left: 40px;
  padding-right: 16px;
  height: 35px;
  display: flex;
  align-items: center;
  position: relative;
  color: var(--text-primary);
  font-size: 14px;
  gap: 4px;
  cursor: pointer;
  transition: 0.2s;
}

.to-sms-number:hover {
  background-color: var(--hover);
}

.to-sms-number-tag {
  font-weight: 700;
}

.to-sms-check {
  position: absolute;
  top: 10px;
  left: 16px;
}

.to-number {
  color: var(--text-primary);
  height: 22px;
  background-color: var(--bg-subtle);
  padding: 0 6px;
  border-radius: 4px;
  gap: 4px;
  display: flex;
}

.to-number-name {
  font-weight: 700;
}

[data-part='arrow'] {
  --arrow-size: 16px;
  --arrow-background: #fff;
  top: v-bind('topArrow') !important;
  left: 24px !important;
}

[data-part='arrow-tip'] {
  border: 1px solid var(--line);
  border-bottom: none;
  border-right: none;
}
</style>
