import { format, subDays } from 'date-fns';
import { useI18n } from 'vue-i18n';
import { es, enUS, ptBR } from 'date-fns/locale';
import { useLanguageStore } from '@/stores/language';

export function getDateDividerChat(dateChat: string | Date) {
  const { t } = useI18n();
  const dateChatFormat = new Date(dateChat);
  const today = new Date();

  if (today.toDateString() === dateChatFormat.toDateString()) {
    return t('Today');
  }

  if (subDays(today, 1).toDateString() === dateChatFormat.toDateString()) {
    return t('Yesterday');
  }

  return format(dateChatFormat, 'EEEE, MMMM do', { locale: getLocaleFormat() });
}

function getLocaleFormat() {
  const { locale } = useLanguageStore();
  if (locale === 'pt_BR') {
    return ptBR;
  }
  if (locale === 'es') {
    return es;
  }
  return enUS;
}
