<template>
  <Teleport to="body">
    <div v-if="api.isOpen">
      <div
        v-bind="api.backdropProps"
        class="attachment-limit-reached-backdrop"
      />
      <div
        v-bind="api.positionerProps"
        class="attachment-limit-reached-container"
      >
        <div v-bind="api.contentProps" class="attachment-limit-reached-content">
          <h2 v-bind="api.titleProps" class="attachment-limit-reached-title">
            {{ t('Attachment limit reached') }}
          </h2>
          <button
            v-bind="api.closeTriggerProps"
            class="attachment-limit-reached-close"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
            >
              <path
                d="M18 6L6 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M6 6L18 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </button>
          <div class="attachment-limit-reached-body-container">
            <div class="attachment-limit-reached-body-content">
              <span>{{ t('You can send up to 10 attachments at once.') }}</span>
            </div>
          </div>
          <div class="attachment-limit-reached-footer">
            <button
              v-bind="api.closeTriggerProps"
              class="primary-btn text-uppercase"
            >
              {{ t('Close') }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import * as dialog from '@zag-js/dialog';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';

interface Emits {
  (e: 'close'): void;
}

const emit = defineEmits<Emits>();

const { t } = useI18n();

const [state, send] = useMachine(
  (dialog as any).machine({
    id: crypto.randomUUID(),
    onClose() {
      emit('close');
    },
    onEsc() {
      emit('close');
    },
    onOutsideClick() {
      emit('close');
    },
  }),
);
const api = computed(() =>
  dialog.connect(state.value as any, send, normalizeProps),
);

onMounted(() => {
  api.value.open();
});
</script>

