<template>
  <div class="editor-footer-container">
    <div class="editor-footer-button-left">
      <button
        class="editor-footer-button-chat"
        :class="{ 'chat-is-not-active': !smsMessageStore.chatIsActive }"
        :disabled="chatDisabled || chatDisabledInject"
        @click="handleChangeToChat"
      >
        CHAT
      </button>
      <button
        class="editor-footer-button-sms"
        :class="{ 'chat-is-active': smsMessageStore.chatIsActive }"
        :disabled="smsIsDisabled(smsDisabled)"
        @click="handleChangeToSms"
      >
        SMS
      </button>
    </div>
    <button
      :disabled="sendDisabled"
      class="editor-footer-button-send"
      :class="{ 'chat-is-not-active': !smsMessageStore.chatIsActive }"
      @click="handleSend"
      @keydown.enter="handleSend"
    >
      <span> {{ t('Send') }}</span>
      <svg
        width="16"
        height="16"
        viewBox="0 0 16 16"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M2.2832 12.9336V9.71681L7.2664 8.00001L2.2832 6.28321V3.06641L14.5664 8.00001L2.2832 12.9336Z"
          fill="white"
        />
      </svg>
    </button>
  </div>
</template>
<script setup lang="ts">
import { useI18n } from 'vue-i18n';
import { computed, inject } from 'vue';
import { useSmsMessageStore } from '@/stores';
import { useRoute } from 'vue-router';
import { RouteName } from '@/enums';
import { useApp, useChat } from '@/composables';

interface Props {
  chatDisabled?: boolean;
  smsDisabled?: boolean;
  sendDisabled?: boolean;
  smsIsActive?: boolean;
}

interface Emit {
  (e: 'send-message'): void;
  (e: 'send-sms'): void;
}

defineProps<Props>();
const emit = defineEmits<Emit>();

const { t } = useI18n();
const smsMessageStore = useSmsMessageStore();
const { smsButtonIsDisabled, isUS } = useApp();
const { numbersFrom, numbersTo } = useChat();
const route = useRoute();

function handleChangeToChat() {
  smsMessageStore.chatIsActiveIsTruthy();
}

function handleChangeToSms() {
  smsMessageStore.chatIsActiveIsFalsy();
}

function handleSend() {
  if (smsMessageStore.chatIsActive) {
    emit('send-message');
    return;
  }
  emit('send-sms');
}

const chatDisabledInject = inject<boolean>('chatDisabled');
const smsIsDisabled = computed(() => {
  return (smsDisabled: boolean | undefined) => {
    const routeName = route.name;
    const hasNumber = !!route.query.number;

    if (
      routeName === RouteName.SendSms ||
      routeName === RouteName.FavoritePhone ||
      hasNumber
    )
      return false;

    if (
      routeName === RouteName.Directory ||
      routeName === RouteName.FavoriteDirectory
    ) {
      return isUS.value
        ? true
        : numbersFrom.value.length === 0 || numbersTo.value.length === 0;
    }

    if (routeName === RouteName.Group || routeName === RouteName.FavoriteGroup)
      return true;

    return smsDisabled || smsButtonIsDisabled.value;
  };
});
</script>

<style>
