  padding: 2px;
  display: flex;
  gap: 2px;
  background-color: var(--bg-subtle);
  font-size: 9px;
  line-height: 12px;
  border-radius: 6px;
  font-weight: 700;
}

.editor-footer-button-chat {
  all: unset;
  cursor: pointer;
  padding: 2px 4px;
  border-radius: 4px;
  background: var(--button-primary-bg);
  color: var(--button-primary-text);
  transition: background-color 200ms, color 200ms;
  display: flex;
  justify-content: center;
  align-items: center;
}

.editor-footer-button-chat:disabled {
  background-color: transparent;
  color: var(--disabled);
  cursor: not-allowed;
}

.editor-footer-button-chat.chat-is-not-active:not(:disabled) {
  background-color: transparent;
  color: var(--button-secondary-text);
}

.editor-footer-button-sms {
  all: unset;
  cursor: pointer;
  background: var(--sms);
  color: var(--button-primary-text);
  padding: 2px 4px;
  border-radius: 4px;
  transition: background-color 200ms, color 200ms;
  display: flex;
  justify-content: center;
  align-items: center;
}

.editor-footer-button-sms:disabled {
  background-color: transparent;
  color: #34476740;
  cursor: not-allowed;
}

.editor-footer-button-sms.chat-is-active:not(:disabled) {
  background-color: transparent;
  color: var(--button-secondary-text);
}

.editor-footer-button-send {
  all: unset;
  cursor: pointer;
  background: var(--button-primary-bg);
  text-transform: uppercase;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 8px;
  gap: 5px;
  color: var(--bg-element);
  font-size: 12px;
  font-weight: 700;
  border-radius: 4px;
  transition: background-color 200ms, color 200ms;
}

.editor-footer-button-send.chat-is-not-active {
  background-color: var(--sms);
}

.editor-footer-button-send:disabled {
  background: transparent;
  color: var(--disabled);
  cursor: not-allowed;
}

.editor-footer-button-send:disabled svg path {
  fill: var(--disabled);
}
</style>
