<template>
  <Teleport to="body">
    <div v-if="api.isOpen">
      <div v-bind="api.backdropProps" class="failed-sms-backdrop" />
      <div v-bind="api.positionerProps" class="failed-sms-container">
        <div v-bind="api.contentProps" class="failed-sms-content">
          <h2 v-bind="api.titleProps" class="failed-sms-title">
            {{ t('Message failed') }}
          </h2>
          <button v-bind="api.closeTriggerProps" class="failed-sms-close">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
            >
              <path
                d="M18 6L6 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M6 6L18 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </button>
          <div class="failed-sms-body-container">
            <div class="failed-sms-body-content">
              <span>{{ t('This message failed to send.') }}</span>
            </div>
          </div>
          <div class="failed-sms-footer">
            <button
              class="primary-btn text-uppercase"
              v-bind="api.closeTriggerProps"
            >
              {{ t('Close') }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import * as dialog from '@zag-js/dialog';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';
import { useSmsMessageStore } from '@/stores';

const { t } = useI18n();
const smsMessageStore = useSmsMessageStore();

const [state, send] = useMachine(
  (dialog as any).machine({
    id: crypto.randomUUID(),
    onClose() {
      smsMessageStore.hasErrorFailedSms = false;
    },
    onEsc() {
      smsMessageStore.hasErrorFailedSms = false;
    },
    onOutsideClick() {
      smsMessageStore.hasErrorFailedSms = false;
    },
  }),
);
const api = computed(() =>
  dialog.connect(state.value as any, send, normalizeProps),
);

onMounted(() => {
  api.value.open();
});
</script>

