<template>
  <div style="z-index: 3">
    <button v-bind="api.triggerProps" class="from-sms">
      <span>
        {{ t('From') }}
      </span>
      <span class="from-number">
        <span v-if="numberSelected.name" class="from-number-name">{{
          numberSelected.name
        }}</span>
        <span>{{ numberSelected.number }}</span>
      </span>
    </button>
    <div
      v-if="api.isOpen && numbersFrom.length > 1"
      v-bind="api.positionerProps"
    >
      <div v-bind="api.arrowProps">
        <div v-bind="api.arrowTipProps" />
      </div>
      <ul v-bind="api.contentProps" ref="contentRef" class="from-sms-numbers">
        <li
          v-for="number in numbersFrom"
          :key="number.numbersUnmasked"
          v-bind="api.getItemProps({ id: number.numbersUnmasked })"
        >
          <div class="from-sms-number">
            <ICheck
              v-if="numberSelected.numbersUnmasked === number.numbersUnmasked"
              size="16"
              class="from-sms-check"
            />
            <span v-if="number.name" class="from-sms-number-tag">{{
              number.name
            }}</span>
            <span>{{ number.number }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script setup lang="ts">
import * as menu from '@zag-js/menu';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted, ref } from 'vue';
import { useI18n } from 'vue-i18n';
import ICheck from '@/components/Icons/ICheck.vue';
import { useChat } from '@/composables';

interface Emit {
  (e: 'src-number', value: string): void;
}

const emit = defineEmits<Emit>();

const { t } = useI18n();
const { numbersFrom } = useChat();

const numberSelected = ref(numbersFrom.value[0]);
const contentRef = ref<HTMLDivElement>();

const topArrow = computed(() => {
  if (contentRef.value) {
    return `${contentRef.value.offsetHeight - 8}px`;
  }
  return '99px';
});

function findIndexMyNumbersByNumber(number: string) {
  return numbersFrom.value.findIndex((n) => n.numbersUnmasked === number);
}

const [state, send] = useMachine(
  (menu as any).machine({
    id: 'from-sms',
    positioning: { placement: 'top-start', offset: { crossAxis: 52 } },
    onSelect(details: { value: string }) {
      const index = findIndexMyNumbersByNumber(details.value);

      if (index !== -1) {
        numberSelected.value = numbersFrom.value[index];
        emit('src-number', numberSelected.value.numbersUnmasked);
      }
    },
  })
);
const api = computed(() =>
  menu.connect(state.value as any, send, normalizeProps)
);

onMounted(() => {
  emit('src-number', numberSelected.value.numbersUnmasked);
});
</script>

<style scoped>
.from-sms {
  all: unset;
  cursor: pointer;
  height: 100%;
  color: var(--text-secondary);
  font-size: 14px;
  padding: 0 8px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.from-sms-numbers {
  background-color: var(--white-500);
  min-width: 248px;
  border-radius: 8px;
  border: 1px solid var(--line);
  list-style: none;
  padding: 0;
  max-height: 107px;
  overflow-y: auto;
}

.from-sms-number {
  padding-left: 40px;
  padding-right: 16px;
  height: 35px;
  display: flex;
  align-items: center;
  position: relative;
  color: var(--text-primary);
  font-size: 14px;
  gap: 4px;
  cursor: pointer;
  transition: 0.2s;
}

.from-sms-number:hover {
  background-color: var(--hover);
}

.from-sms-number-tag {
  font-weight: 700;
}

.from-sms-check {
  position: absolute;
  top: 10px;
  left: 16px;
}

.from-number {
  color: var(--text-primary);
  height: 22px;
  background-color: var(--bg-subtle);
  padding: 0 6px;
  border-radius: 4px;
  gap: 4px;
  display: flex;
}

.from-number-name {
  font-weight: 700;
}

[data-part='arrow'] {
  --arrow-size: 16px;
  --arrow-background: #fff;
  top: v-bind('topArrow') !important;
  left: 24px !important;
}

[data-part='arrow-tip'] {
  border: 1px solid var(--line);
  border-bottom: none;
  border-right: none;
}
</style>
