<script setup lang="ts">
import * as dialog from '@zag-js/dialog';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';
import { Account } from '@/enums';
import type { UserTypeAccount } from '@/types';
import { useSmsMessageStore, useUserStore } from '@/stores';
import { useApp } from '@/composables';

interface Props {
  number: string;
}

const props = defineProps<Props>();

const { t } = useI18n();
const { isUS } = useApp();
const userStore = useUserStore();
const smsMessageStore = useSmsMessageStore();

const [state, send] = useMachine(
  (dialog as any).machine({
    id: crypto.randomUUID(),
    onClose() {
      smsMessageStore.hasErrorEnablingSms = false;
    },
    onEsc() {
      smsMessageStore.hasErrorEnablingSms = false;
    },
    onOutsideClick() {
      smsMessageStore.hasErrorEnablingSms = false;
    },
  }),
);
const api = computed(() =>
  dialog.connect(state.value as any, send, normalizeProps),
);

const isAdmin = computed(
  () => Account[userStore.currentLevel as UserTypeAccount] === 'Account Admin',
);

const messages = {
  us: {
    admin: `SMS is not available for the number <span class="enabling-sms-number">${props.number}</span>. To enable SMS features, request SMS activation for this account.`,
    user: `SMS is not available for the number <span class="enabling-sms-number">${props.number}</span>. Ask an account Admin to request SMS activation.`,
  },
  au: {
    admin: `SMS is not available for the number <span class="enabling-sms-number">${props.number}</span>. To enable SMS features, add a mobile number to your account.`,
    user: `SMS is not available for the number <span class="enabling-sms-number">${props.number}</span>. To enable SMS features, ask an account Admin to add a mobile number.`,
  },
};

const message = computed(() => {
  return messages[isUS.value ? 'us' : 'au'][isAdmin.value ? 'admin' : 'user'];
});

onMounted(() => {
  console.log('mounted');
  api.value.open();
});
</script>

