<template>
  <button
    class="attachment-file-button"
    :class="{ 'attachment-file-button-items': filesLength > 0 }"
    @click="() => open()"
  >
    <svg
      width="18"
      height="18"
      viewBox="0 0 18 18"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M8.55025 16.3692C7.35025 16.3692 6.34705 15.9441 5.54066 15.0939C4.73425 14.2437 4.33105 13.2249 4.33105 12.0375V4.6692C4.33105 3.819 4.62805 3.0939 5.22205 2.4939C5.81605 1.8939 6.53785 1.5939 7.38745 1.5939C8.26286 1.5939 8.99425 1.9095 9.58165 2.5407C10.1691 3.1719 10.4628 3.9186 10.4628 4.7808V11.6811C10.4628 12.2061 10.2723 12.6531 9.89125 13.0221C9.50965 13.3905 9.06265 13.5747 8.55025 13.5747C8.00005 13.5747 7.54375 13.3749 7.18135 12.9753C6.81895 12.5751 6.63775 12.1125 6.63775 11.5875V4.4253H8.13805V11.6811C8.13805 11.7939 8.17855 11.8908 8.25955 11.9718C8.34055 12.0534 8.43745 12.0942 8.55025 12.0942C8.66305 12.0942 8.75995 12.0534 8.84095 11.9718C8.92195 11.8908 8.96245 11.7939 8.96245 11.6811V4.6692C8.96245 4.2312 8.80945 3.8562 8.50345 3.5442C8.19745 3.2316 7.82545 3.0753 7.38745 3.0753C6.95005 3.0753 6.57835 3.2346 6.27235 3.5532C5.96575 3.8718 5.81245 4.2561 5.81245 4.7061V12.1689C5.81245 12.9315 6.08125 13.5753 6.61885 14.1003C7.15645 14.6253 7.80025 14.8878 8.55025 14.8878C9.33745 14.8878 9.99055 14.6097 10.5096 14.0535C11.0286 13.4973 11.2881 12.8379 11.2881 12.0753V4.4253H12.7695V12.1689C12.7695 13.3437 12.363 14.3373 11.55 15.1497C10.7376 15.9627 9.73765 16.3692 8.55025 16.3692Z"
        fill="#444444"
      />
    </svg>
    <span v-if="filesLength > 0" class="attachment-file-button-text">{{
      filesLength
    }}</span>
  </button>
</template>

<script setup lang="ts">
import { useFileDialog } from '@vueuse/core';
import { watchEffect } from 'vue';

interface Props {
  filesLength: number;
}

interface Emits {
  (e: 'on-change', files: File[]): void;
}
const emit = defineEmits<Emits>();
defineProps<Props>();

const { open, files, reset } = useFileDialog();

watchEffect(() => {
  if (files.value && files.value?.length > 0) {
    emit('on-change', Array.from(files.value));
    reset();
  }
});
</script>

