<template>
  <Teleport to="body">
    <div v-if="api.isOpen">
      <div v-bind="api.backdropProps" class="attachment-delete-backdrop" />
      <div v-bind="api.positionerProps" class="attachment-delete-container">
        <div v-bind="api.contentProps" class="attachment-delete-content">
          <h2 v-bind="api.titleProps" class="attachment-delete-title">
            {{ t('Delete attachment') }}
          </h2>
          <button
            v-bind="api.closeTriggerProps"
            class="attachment-delete-close"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
            >
              <path
                d="M18 6L6 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M6 6L18 18"
                stroke="#697E8E"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </button>
          <div class="attachment-delete-body-container">
            <div class="attachment-delete-body-content">
              <span
                >{{ t('Are you sure you wish to delete ')
                }}<strong>{{ fileData.name }}?</strong></span
              >
            </div>
          </div>
          <div class="attachment-delete-footer">
            <button
              class="secondary-btn text-uppercase"
              v-bind="api.closeTriggerProps"
            >
              {{ t('Cancel') }}
            </button>
            <button
              v-bind="api.closeTriggerProps"
              class="primary-btn text-uppercase"
              @click="emit('delete', fileData.id)"
            >
              {{ t('Delete') }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import * as dialog from '@zag-js/dialog';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';
import type { FileData } from '@/types';

interface Emits {
  (e: 'delete', id: string): void;
  (e: 'close'): void;
}

interface Props {
  fileData: FileData;
}

defineProps<Props>();
const emit = defineEmits<Emits>();

const { t } = useI18n();

const [state, send] = useMachine(
  (dialog as any).machine({
    id: crypto.randomUUID(),
    onClose() {
      emit('close');
    },
    onEsc() {
      emit('close');
    },
    onOutsideClick() {
      emit('close');
    },
  }),
);
const api = computed(() =>
  dialog.connect(state.value as any, send, normalizeProps),
);

onMounted(() => {
  console.log(api.value.closeTriggerProps);
  api.value.open();
});
</script>

