<template>
  <button
    v-tippy="{}"
    type="button"
    class="upload-failed-button"
    data-bs-toggle="tooltip"
    data-bs-placement="top"
    :title="title"
    @click="handleTryAgain(file, index)"
  >
    <svg
      width="16"
      height="17"
      viewBox="0 0 16 17"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M0.799805 14.1L7.9998 2.10001L15.1998 14.1H0.799805ZM2.91647 12.9H13.0831L7.9998 4.43334L2.91647 12.9ZM7.99629 12.1C8.1653 12.1 8.30814 12.0428 8.4248 11.9285C8.54147 11.8142 8.5998 11.6725 8.5998 11.5035C8.5998 11.3345 8.54264 11.1917 8.42832 11.075C8.31399 10.9583 8.17232 10.9 8.00332 10.9C7.83431 10.9 7.69147 10.9572 7.5748 11.0715C7.45814 11.1858 7.3998 11.3275 7.3998 11.4965C7.3998 11.6655 7.45697 11.8083 7.57129 11.925C7.68562 12.0417 7.82729 12.1 7.99629 12.1ZM7.3998 10.1H8.5998V6.90001H7.3998V10.1Z"
        fill="#DC001E"
      />
    </svg>
  </button>
</template>

<script setup lang="ts">
import type { FileUploadData } from '@/types';

interface Props {
  title: string;
  index: number;
  file: FileUploadData;
}

interface Emits {
  (e: 'try-again', index: number): void;
}

const emit = defineEmits<Emits>();
defineProps<Props>();

function handleTryAgain(file: FileUploadData, index: number) {
  if (file.tryAgain) {
    emit('try-again', index);
  }
}
</script>

<style scoped>
.upload-failed-button {
  all: unset;
  background-color: transparent;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
