<template>
  <div class="editor-header-container">
    <BaseFormat
      v-for="(format, index) in formats"
      :key="index"
      v-bind="format"
      :editor="editor"
    />
  </div>
</template>

<script setup lang="ts">
import type { Editor } from '@tiptap/vue-3';
import BaseFormat from '@/components/Editor/BaseFormat.vue';
import IBlockquote from '@/components/Editor/Icons/IBlockquote.vue';
import IBold from '@/components/Editor/Icons/IBold.vue';
import IItalic from '@/components/Editor/Icons/IItalic.vue';
import IStrike from '@/components/Editor/Icons/IStrike.vue';
import ICodeBlock from '@/components/Editor/Icons/ICodeBlock.vue';
import IBulletList from '@/components/Editor/Icons/IBulletList.vue';
import IOrderedList from '@/components/Editor/Icons/IOrderedList.vue';

interface Props {
  editor: Editor;
}

const props = defineProps<Props>();

const formats = [
  {
    formatName: 'bold',
    icon: IBold,
    onClick: () => props.editor.chain().focus().toggleBold().run(),
  },
  {
    formatName: 'italic',
    icon: IItalic,
    onClick: () => props.editor.chain().focus().toggleItalic().run(),
  },
  {
    formatName: 'strike',
    icon: IStrike,
    onClick: () => props.editor.chain().focus().toggleStrike().run(),
  },
  {
    formatName: 'blockquote',
    icon: IBlockquote,
    onClick: () => props.editor.chain().focus().toggleBlockquote().run(),
  },
  {
    formatName: 'codeBlock',
    icon: ICodeBlock,
    onClick: () => props.editor.chain().focus().toggleCodeBlock().run(),
  },
  {
    formatName: 'bulletList',
    icon: IBulletList,
    onClick: () => props.editor.chain().focus().toggleBulletList().run(),
  },
  {
    formatName: 'orderedList',
    icon: IOrderedList,
    onClick: () => props.editor.chain().focus().toggleOrderedList().run(),
  },
];
</script>

<style scoped>
.editor-header-container {
  height: 36px;
  width: 100%;
  background-color: white;
  padding: 4px 8px;
  display: flex;
  align-items: center;
  gap: 2px;
  border-top: 1px solid var(--line);
}
</style>
