<template>
  <EditorHeader v-if="headerIsVisible && editor" :editor="editor" />
  <div ref="containerRef" class="base-editor-container" @paste="onPaste">
    <DropZone v-show="dropZoneIsActive" />
    <PerfectScrollbar ref="scrollbarRef" class="w-100">
      <EditorContent
        v-show="!dropZoneIsActive"
        id="tip-tap"
        :editor="editor as Editor"
        @keydown.enter="handleSendSmsEnter"
      />
    </PerfectScrollbar>
    <BaseAttachments
      v-show="!dropZoneIsActive"
      v-if="filesData.length > 0 && smsMessageStore.chatIsActive"
      :files-data="filesData"
      @remove-item="handleRemoveAttachment"
      @try-again="onTryAgain"
    />
  </div>
  <footer class="base-editor-footer">
    <FooterOpacity />
    <div>
      <AttachmentFile
        :disabled="!smsMessageStore.chatIsActive"
        :files-length="filesData.length"
        @on-change="onChange"
      />
    </div>
    <EditorFooter
      :send-disabled="sendIsDisabled"
      :change-chat-to-sms="showSmsOnApp()"
      @send-sms="handleSendSms"
      @send-message="handleSendMessage"
    />
  </footer>
  <AttachmentDelete
    v-if="modalDeleteAttachment"
    :file-data="fileData as FileData"
    @delete="handleDeleteAttachment"
    @close="modalDeleteAttachment = false"
  />
  <AttachmentLimitReached
    v-if="modalAttachmentLimitReached"
    @close="onCloseModalAttachmentLimitReached"
  />
  <AttachmentFileSizeExceeded
    v-if="modalAttachmentLimitExceeded"
    :file-data="filesDataLimitExceeded[0]"
    @close="handleCloseModalAttachmentLimitExceeded"
  />
  <AttachmentNotAllowed
    v-if="modalNotAllowed"
    :file-data="filesDataNotAllowed[0]"
    @close="handleCloseModalNotAllowed"
  />
  <AttachmentTryAgain
    v-if="typeof tryAgainAttachmentIndex === 'number'"
    :name="filesData[tryAgainAttachmentIndex].name"
    :is-loading="isLoadingTryAgain"
    @close="clearTryAgainAttachment"
    @try-again="handleTryAgain"
  />
</template>

<script lang="ts" setup>
import { useEditor, EditorContent } from '@tiptap/vue-3';
import type { Editor } from '@tiptap/vue-3';
import StarterKit from '@tiptap/starter-kit';
import Placeholder from '@tiptap/extension-placeholder';
import Bold from '@tiptap/extension-bold';
import Italic from '@tiptap/extension-italic';
import Strike from '@tiptap/extension-strike';
import Link from '@tiptap/extension-link';
import Blockquote from '@tiptap/extension-blockquote';
import CodeBlock from '@tiptap/extension-code-block';
import BulletList from '@tiptap/extension-bullet-list';
import Document from '@tiptap/extension-document';
import ListItem from '@tiptap/extension-list-item';
import OrderedList from '@tiptap/extension-ordered-list';
import Paragraph from '@tiptap/extension-paragraph';
import Text from '@tiptap/extension-text';
import { Extension } from '@tiptap/core';
import { useI18n } from 'vue-i18n';
import EditorFooter from '@/components/Editor/EditorFooter.vue';
import { computed, onBeforeUnmount, onMounted, ref, watch } from 'vue';
import { useRoute } from 'vue-router';
import { RouteName } from '@/enums';
import { useDropZone } from '@vueuse/core';
import {
  useAttachmentStore,
  useSmsMessageStore,
  useTypingStore,
} from '@/stores';
import BaseAttachments from '@/components/Attachments/BaseAttachments.vue';
import type { FileData, FileUploadData, PutFileResponseData } from '@/types';
import AttachmentDelete from '@/components/Attachments/AttachmentDelete.vue';
import AttachmentLimitReached from '@/components/Attachments/AttachmentLimitReached.vue';
import AttachmentFileSizeExceeded from '@/components/Attachments/AttachmentFileSizeExceeded.vue';
import AttachmentNotAllowed from '@/components/Attachments/AttachmentNotAllowed.vue';
import DropZone from '@/components/Attachments/DropZone.vue';
import EditorHeader from '@/components/Editor/EditorHeader.vue';
import AttachmentFile from '@/components/Attachments/AttachmentFile.vue';
import { useAttachments, useChat } from '@/composables';
import type { AxiosError, AxiosProgressEvent } from 'axios';
import type { ApiDataException } from '@/types';
import { showSmsOnApp } from '@/utils/helper';
import AttachmentTryAgain from '@/components/Attachments/AttachmentTryAgain.vue';
import FooterOpacity from '@/components/SMS/Fotter/FooterOpacity.vue';

const isTest = import.meta.env.VITE_ONLY_TESTS === 'true';

interface Props {
  headerIsVisible: boolean;
}

interface PScrollBar {
  $el: Element;
  ps: {
    contentHeight: number;
    element: Element;
  };
}

interface Emit {
  (e: 'send-sms', content: string): void;
  (e: 'send-message', content: string): void;
  (
    e: 'send-message-attachment',
    files: FileUploadData[],
    content: string | undefined
  ): void;
  (e: 'height', diff: number): void;
}

const route = useRoute();
const smsMessageStore = useSmsMessageStore();
const typingStore = useTypingStore();
const { putFile, putGroupFile } = useAttachments();
const attachmentStore = useAttachmentStore();
const { number } = useChat();

defineProps<Props>();
const emit = defineEmits<Emit>();

const { t } = useI18n();

const DisableEnter = Extension.create({
  addKeyboardShortcuts() {
    return {
      Enter: () => true,
    };
  },
});

const NewLine = Extension.create({
  addKeyboardShortcuts() {
    return {
      'Shift-Enter': ({ editor }) =>
        editor.commands.first(({ commands }) => [
          () => commands.newlineInCode(),
          () => commands.splitListItem('listItem'), // This line added
          () => commands.createParagraphNear(),
          () => commands.liftEmptyBlock(),
          () => commands.splitBlock(),
        ]),
    };
  },
});

const editor = useEditor({
  extensions: [
    StarterKit,
    Placeholder.configure({
      placeholder: t('Type your message here'),
    }),
    Bold,
    Italic,
    Strike,
    Blockquote,
    Link.configure({
      HTMLAttributes: {
        style: 'cursor: pointer;',
      },
    }),
    DisableEnter,
    NewLine,
    CodeBlock,
    BulletList,
    Document,
    ListItem,
    OrderedList,
    Paragraph,
    Text,
  ],
  editorProps: {
    handleDOMEvents: {
      drop: (_, e) => e.preventDefault(),
    },
  },
  editable:
    route.name !== RouteName.FavoriteContact &&
    route.name !== RouteName.Contact,
});

const containerRef = ref<HTMLDivElement>();
const sendIsDisabled = ref(true);

const filesData = ref<FileUploadData[]>([]);
const filesDataLimitExceeded = ref<FileData[]>([]);
const filesDataNotAllowed = ref<FileUploadData[]>([]);
const fileData = ref<FileData>();
const uploadLingFile = ref(false);

const modalDeleteAttachment = ref(false);
const modalAttachmentLimitReached = ref(false);
const modalAttachmentLimitExceeded = ref(false);
const modalNotAllowed = ref(false);

const tryAgainAttachmentIndex = ref<number>();
const isLoadingTryAgain = ref(false);

const controller = new AbortController();

const dropZoneIsActive = computed(() => {
  if (!smsMessageStore.chatIsActive) return false;

  return isOverDropZone.value;
});

const scrollbarRef = ref<PScrollBar>();
const containerRefHeight = ref(0);

function setFileData(response: PutFileResponseData, index: number) {
  filesData.value[index] = {
    ...filesData.value[index],
    size: response.size,
    hash: response.hash,
    filetype: response.fileType,
    progress: 100,
    success: response.success,
    message: response.message,
    tryAgain: false,
  };
}

function onProgressPutFile(event: AxiosProgressEvent, index: number) {
  filesData.value[index].progress = event.progress
    ? Number((event.progress * 100).toFixed(0))
    : 100;
}

function errorPutFile(e: unknown, index: number) {
  const error = e as AxiosError<ApiDataException>;

  filesData.value[index] = {
    ...filesData.value[index],
    progress: 100,
    success: false,
    message: 'Failed',
    tryAgain: false,
  };

  if (error.response) {
    const notAllowedMessage = "This type of file it's not allowed";
    const LimitExceededMessage = 'Files have to be under';

    if (error.response.data.message === notAllowedMessage) {
      filesDataNotAllowed.value.push(filesData.value[index]);
      filesData.value.splice(index, 1);
      return;
    }

    if (error.response.data.message.startsWith(LimitExceededMessage)) {
      filesDataLimitExceeded.value.push(filesData.value[index]);
      filesData.value.splice(index, 1);
      return;
    }
    filesData.value[index].tryAgain = true;
    return;
  }

  filesData.value[index].tryAgain = true;
}

async function uploadFile(file: File, index: number) {
  try {
    const response = await putFile({
      file,
      to: String(route.params.id),
      onProgress: (event) => {
        onProgressPutFile(event, index);
      },
      signal: controller.signal,
    });

    setFileData(response, index);
  } catch (e) {
    errorPutFile(e, index);
  }
}

async function uploadGroupFile(file: File, index: number) {
  const groupId = String(route.params.id);

  try {
    const response = await putGroupFile({
      file,
      groupId,
      onProgress: (event) => {
        onProgressPutFile(event, index);
      },
      signal: controller.signal,
    });

    setFileData(response, index);
  } catch (e) {
    errorPutFile(e, index);
  }
}

async function caseUploadFile(file: FileUploadData, index: number) {
  const routeName = route.name;

  switch (routeName) {
    case RouteName.Group:
    case RouteName.FavoriteGroup:
      await uploadGroupFile(file.file as File, index);
      break;
    case RouteName.Directory:
    case RouteName.FavoriteDirectory:
    default:
      await uploadFile(file.file as File, index);
  }
}

function setLimitExceeded(file: File) {
  filesDataLimitExceeded.value.push({
    id: window.crypto.randomUUID(),
    size: file.size,
    name: file.name,
    type: file.type,
    lastModified: file.lastModified,
  });
  setTimeout(() => {
    modalAttachmentLimitExceeded.value = true;
  }, 500);
}

function setDataNotAllowed(file: FileUploadData) {
  filesDataNotAllowed.value.push(file);
  setTimeout(() => {
    modalNotAllowed.value = true;
  }, 500);
}

async function onDrop(files: File[] | null) {
  if (!smsMessageStore.chatIsActive || !isTest) return;

  filesDataLimitExceeded.value = [];
  if (files) {
    if (files.length + filesData.value.length > 10) {
      setTimeout(() => {
        modalAttachmentLimitReached.value = true;
      }, 500);
    }

    for await (const file of files) {
      if (filesData.value.length >= 10) break;

      const fileData = {
        id: window.crypto.randomUUID(),
        name: file.name,
        size: file.size,
        type: file.type,
        lastModified: file.lastModified,
        hash: '',
        filetype: '',
        progress: 0,
        success: false,
        message: '',
        file,
        tryAgain: false,
      };

      if (file.size > attachmentStore.maxSizeFileBytes) {
        setLimitExceeded(file);
        continue;
      }

      if (attachmentStore.isForbiddenFileTypes(file.name)) {
        setDataNotAllowed(fileData);
        continue;
      }

      filesData.value.push(fileData);
    }

    for await (const [index, file] of filesData.value.entries()) {
      if (file.progress !== 0 && file.progress <= 100) continue;
      await caseUploadFile(file, index);
    }
  }
}

function afterOnChange() {
  setTimeout(() => {
    const hasUploadFile = filesData.value.some((file) => file.success);

    if (hasUploadFile && !uploadLingFile.value) {
      sendIsDisabled.value = false;
    }

    if (filesDataNotAllowed.value.length > 0) {
      modalNotAllowed.value = true;
      return;
    }

    if (filesDataLimitExceeded.value.length > 0) {
      modalAttachmentLimitExceeded.value = true;
    }
  }, 500);
}

async function onChange(files: File[] | null) {
  sendIsDisabled.value = true;
  uploadLingFile.value = true;
  await onDrop(files);
  uploadLingFile.value = false;

  afterOnChange();
}

function handleRemoveAttachment(id: string) {
  const index = filesData.value.findIndex((file) => file.id === id);

  if (index !== -1) {
    fileData.value = filesData.value[index];
    modalDeleteAttachment.value = true;
  }
}

function handleDeleteAttachment(id: string) {
  const index = filesData.value.findIndex((file) => file.id === id);

  if (index !== -1) {
    filesData.value.splice(index, 1);
  }
  const text = editor.value?.getText();
  if (filesData.value.length === 0 && !text) {
    sendIsDisabled.value = true;
  }
}

function handleSendSms() {
  const content = editor.value?.getText();

  if (!content) return;

  emit('send-sms', String(content));
  editor.value?.commands.clearContent();
  editor.value?.commands.focus();
  sendIsDisabled.value = true;
  (scrollbarRef.value as PScrollBar).$el.scrollTop = 0;
}

function handleSendMessage() {
  if (uploadLingFile.value) return;

  const html = editor.value?.getHTML();
  const text = editor.value?.getText();

  if (filesData.value.length > 0) {
    const files = filesData.value.filter((file) => file.success);
    emit('send-message-attachment', files, html);
    editor.value?.commands.clearContent();
    editor.value?.commands.focus();
    filesData.value = [];
    sendIsDisabled.value = true;
    (scrollbarRef.value as PScrollBar).$el.scrollTop = 0;
    return;
  }

  if (text) {
    emit('send-message', html as string);
    editor.value?.commands.clearContent();
    editor.value?.commands.focus();
    sendIsDisabled.value = true;
    (scrollbarRef.value as PScrollBar).$el.scrollTop = 0;
  }
}

function handleSendSmsEnter(event: KeyboardEvent) {
  if (
    event.shiftKey ||
    (!number.value && route.name === RouteName.Sms) ||
    sendIsDisabled.value
  )
    return;
  if (smsMessageStore.chatIsActive) {
    handleSendMessage();
    sendIsDisabled.value = true;
    return;
  }
  handleSendSms();
}

function editorOnUpdate() {
  editor.value?.on('update', () => {
    const text = editor.value?.getText();
    const routeName = String(route.name);

    if (routeName === RouteName.Sms && !number.value) return;

    if (text && text.length > 0 && !uploadLingFile.value) {
      sendIsDisabled.value = false;
      return;
    }

    const hasUploadFile = filesData.value.some((file) => file.success);

    if (!text && !hasUploadFile) {
      sendIsDisabled.value = true;
    }
  });
}

function handleCloseModalAttachmentLimitExceeded() {
  modalAttachmentLimitExceeded.value = false;

  setTimeout(() => {
    if (filesDataLimitExceeded.value.length > 1) {
      filesDataLimitExceeded.value.shift();
      modalAttachmentLimitExceeded.value = true;
      return;
    }

    filesDataLimitExceeded.value = [];
  }, 50);
}

function handleCloseModalNotAllowed() {
  modalNotAllowed.value = false;

  setTimeout(() => {
    if (filesDataNotAllowed.value.length > 1) {
      filesDataNotAllowed.value.shift();
      modalNotAllowed.value = true;
      return;
    }

    if (filesDataLimitExceeded.value.length > 0) {
      modalAttachmentLimitExceeded.value;
    }

    filesDataNotAllowed.value = [];
  }, 50);
}

const { isOverDropZone } = useDropZone(containerRef, onChange);

function onPaste(e: ClipboardEvent) {
  e.preventDefault();
  const items = e.clipboardData?.items;

  if (items) {
    const files: File[] = [];
    for (const item of items) {
      if (item.kind === 'file') {
        files.push(item.getAsFile() as File);
      }
    }
    if (files.length > 0) {
      onChange(files);
    }
  }
}

function onCloseModalAttachmentLimitReached() {
  modalAttachmentLimitReached.value = false;

  if (filesDataLimitExceeded.value.length > 0) {
    modalAttachmentLimitExceeded.value = true;
  }
}

function changeHeightEditor() {
  const resize_ob = new ResizeObserver(function (entries) {
    const rect = entries[0].contentRect;

    const height = rect.height;

    if (height === 0) return;

    const comparative = height > containerRefHeight.value;
    if (scrollbarRef.value && comparative) {
      emit('height', height - containerRefHeight.value);
    }

    containerRefHeight.value = height;
  });

  resize_ob.observe(containerRef.value as HTMLDivElement);
}

function focusEditor() {
  const routeName = route.name;
  if (routeName !== RouteName.Sms) {
    editor.value?.commands.focus();
  }
}

function clearFiles() {
  filesData.value = [];
  filesDataNotAllowed.value = [];
  filesDataLimitExceeded.value = [];
}

function onTryAgain(index: number) {
  isLoadingTryAgain.value = false;
  tryAgainAttachmentIndex.value = index;
}

async function handleTryAgain() {
  if (typeof tryAgainAttachmentIndex.value !== 'number') return;
  isLoadingTryAgain.value = true;

  const index = tryAgainAttachmentIndex.value;
  const file = filesData.value[index];

  sendIsDisabled.value = true;
  uploadLingFile.value = true;
  file.progress = 0;

  try {
    await caseUploadFile(file, index);
  } finally {
    uploadLingFile.value = false;
    tryAgainAttachmentIndex.value = undefined;
    afterOnChange();
  }
}

function clearTryAgainAttachment() {
  tryAgainAttachmentIndex.value = undefined;
}

watch(
  () => route.params.id,
  () => {
    editor.value?.commands.clearContent();
    focusEditor();
    clearFiles();
    controller.abort();
  }
);

watch(
  () => route.query,
  (query) => {
    const text = editor.value?.getText();
    if (query && text && text.length > 0) {
      sendIsDisabled.value = false;
    }
  }
);

onMounted(() => {
  editorOnUpdate();
  changeHeightEditor();

  setTimeout(() => {
    if (containerRef.value) {
      containerRefHeight.value =
        containerRef.value.getBoundingClientRect().height - 2;
    }
    typingStore.handleKeydownTyping();
    focusEditor();
  }, 200);
});

onBeforeUnmount(() => {
  controller.abort();
});
</script>

<style scoped>
.base-editor-container {
  border-top: 1px solid var(--line);
  border-bottom: 1px solid var(--line);
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: var(--bg-element);
}

.base-editor-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
  height: 36px;
  background-color: var(--bg-element);
  position: relative;
}
</style>
<style>
.ProseMirror {
  min-height: 98px;
  max-height: calc(50vh - 80px);
  background-color: var(--bg-element);
  padding: 8px;
  font-size: 14px;
  line-height: 19px;
  color: var(--text-primary);
  width: 100%;
}

.ProseMirror[contenteditable='false'] {
  cursor: not-allowed;
}
.ProseMirror p.is-editor-empty:first-child::before {
  color: var(--input-text-placeholder);
  content: attr(data-placeholder);
  float: left;
  height: 0;
  pointer-events: none;
}

.ProseMirror p {
  margin: 0;
}

.ProseMirror blockquote {
  border-left-color: var(--line);
  border-left-style: solid;
  border-left-width: 2px;
  color: var(--text-primary);
  margin: 4px 0;
  padding-left: 8px;
}

.ProseMirror code {
  background-color: #f3f5f9;
  border: 1px solid var(--line);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 12px;
  margin: 4px 0;
  padding: 8px;
  display: block;
}
</style>
