<template>
  <Teleport to="body">
    <div v-if="api.isOpen">
      <div v-bind="api.backdropProps" class="attachment-try-again-backdrop" />
      <div v-bind="api.positionerProps" class="attachment-try-again-container">
        <div v-bind="api.contentProps" class="attachment-try-again-content">
          <h2 v-bind="api.titleProps" class="attachment-try-again-title">
            {{ t('Upload failed') }}
          </h2>
          <button
            v-bind="api.closeTriggerProps"
            class="attachment-try-again-close"
          >
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M18 6L6 18"
                stroke="#808080"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
              <path
                d="M6 6L18 18"
                stroke="#808080"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
              />
            </svg>
          </button>
          <div class="attachment-try-again-body-container">
            <div class="attachment-try-again-body-content">
              <span class="font-weight-700">{{ name }}</span>
              <span>{{
                t(' could not be added as an attachment to your message.')
              }}</span>
            </div>
          </div>
          <div class="attachment-try-again-footer">
            <button
              v-bind="api.closeTriggerProps"
              class="secondary-btn text-uppercase"
              :disabled="isLoading"
            >
              {{ t('Cancel') }}
            </button>
            <button
              :disabled="isLoading"
              class="primary-btn text-uppercase"
              @click="handleTryAgain"
            >
              <BaseIcon
                v-if="isLoading"
                name="fa-solid fa-spinner"
                class="spin me-1"
                library="fontawesome"
                font-size="1rem"
              ></BaseIcon>
              {{ t('Try Again') }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import * as dialog from '@zag-js/dialog';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed, onMounted } from 'vue';
import { useI18n } from 'vue-i18n';
import BaseIcon from '@/components/BaseIcon.vue';

interface Props {
  name: string;
  isLoading: boolean;
}

interface Emits {
  (e: 'try-again'): void;
  (e: 'close'): void;
}

defineProps<Props>();
const emit = defineEmits<Emits>();

const { t } = useI18n();

const [state, send] = useMachine(
  (dialog as any).machine({
    id: crypto.randomUUID(),
    onClose() {
      emit('close');
    },
    onEsc() {
      emit('close');
    },
    onOutsideClick() {
      emit('close');
    },
  }),
);
const api = computed(() =>
  dialog.connect(state.value as any, send, normalizeProps),
);

function handleTryAgain() {
  emit('try-again');
}

onMounted(() => {
  api.value.open();
});
</script>

