<template>
  <div class="base-footer-sms-container">
    <div v-if="isVisible" class="base-footer-number">
      <FromSms @src-number="changeSrcNumber" />
      <Separator orientation="vertical" class="SeparatorRoot" />
      <ToSms @dst-number="changeDstNumber" />
    </div>
    <BaseEditor
      :header-is-visible="!isVisible"
      @send-sms="handleSendSms"
      @send-message="handleSendMessage"
      @send-message-attachment="handleSendMessageAttachment"
      @height="(diff) => emit('height', diff)"
    />
  </div>
  <EnablingSms
    v-if="smsMessageStore.hasErrorEnablingSms"
    :number="phoneMasked(smsMessageStore.number)"
  />
  <FailedSms v-if="smsMessageStore.hasErrorFailedSms" />
</template>

<script setup lang="ts">
import BaseEditor from '@/components/Editor/BaseEditor.vue';
import ToSms from '@/components/SMS/Fotter/ToSms.vue';
import FromSms from '@/components/SMS/Fotter/FromSms.vue';
import {
  useGroupMessageStore,
  useMessageStore,
  useSmsMessageStore,
  useUserStore,
  useAttachmentStore,
} from '@/stores';
import { computed, onMounted, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { RouteName } from '@/enums';
import EnablingSms from '@/components/SMS/Modals/EnablingSms.vue';
import { phoneMasked } from '@/utils';
import FailedSms from '@/components/SMS/Modals/FailedSms.vue';
import type { FileUploadData } from '@/types';
import { useChat } from '@/composables';
import type { Ref } from 'vue';
import { inject } from 'vue';
import { Separator } from 'radix-vue';

const width = inject<Ref<string>>('width');

interface Emit {
  (e: 'dst-number', value: string): void;
  (e: 'height', diff: number): void;
}

const emit = defineEmits<Emit>();

const route = useRoute();
const router = useRouter();
const smsMessageStore = useSmsMessageStore();
const messageStore = useMessageStore();
const groupMessageStore = useGroupMessageStore();
const userStore = useUserStore();
const attachmentStore = useAttachmentStore();
const { number, numbersTo } = useChat();

const srcNumber = ref('');
const dstNumber = ref('');

const isVisible = computed(() => {
  const routeName = route.name;

  if (
    smsMessageStore.chatIsActive ||
    routeName === RouteName.Contact ||
    routeName === RouteName.FavoriteContact
  )
    return false;

  if (
    routeName === RouteName.Directory ||
    routeName === RouteName.FavoriteDirectory ||
    routeName === RouteName.SendSms ||
    routeName === RouteName.FavoritePhone
  )
    return true;
  return !!route.query.number;
});

function changeSrcNumber(number: string) {
  srcNumber.value = number;
}

function changeDstNumber(number: string) {
  dstNumber.value = number;
  emit('dst-number', number);
}

function handleSendSms(content: string) {
  smsMessageStore.sendSms({
    content,
    srcNumber: srcNumber.value,
    dstNumber: dstNumber.value,
  });
}

function handleSendMessage(content: string) {
  const id = String(route.params.id);

  if (
    route.name === RouteName.Directory ||
    route.name === RouteName.FavoriteDirectory
  ) {
    messageStore.newMessage(parseInt(id, 10), content);
    return;
  }

  if (
    route.name === RouteName.Group ||
    route.name === RouteName.FavoriteGroup
  ) {
    groupMessageStore.newGroupMessage(
      content,
      userStore.groupUsersById(Number(id))
    );
    return;
  }
}

function handleSendMessageAttachment(
  files: FileUploadData[],
  content: string | undefined
) {
  const id = String(route.params.id);
  content = content === '<p></p>' ? undefined : content;

  if (
    route.name === RouteName.Directory ||
    route.name === RouteName.FavoriteDirectory
  ) {
    attachmentStore.postAttachment({
      dstUser: parseInt(id, 10),
      message: content,
      files,
    });
    return;
  }

  if (
    route.name === RouteName.Group ||
    route.name === RouteName.FavoriteGroup
  ) {
    groupMessageStore.newGroupMessageAttachment({
      users: userStore.groupUsersById(Number(id)),
      message: content,
      files,
    });
    return;
  }
}

function changeChatIsActive() {
  if (
    route.name === RouteName.Sms ||
    route.name === RouteName.SendSms ||
    numbersTo.value.some((n) => n === number.value)
  ) {
    smsMessageStore.chatIsActiveIsFalsy();
    return;
  }

  smsMessageStore.chatIsActiveIsTruthy();

  if (route.name !== RouteName.Sms) {
    router.replace(route.path);
  }
}

onMounted(() => {
  changeChatIsActive();
});
</script>

<style scoped>
.base-footer-sms-container {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-subtle);
  width: v-bind('width');
  position: relative;
}

.base-footer-number {
  display: flex;
  background-color: var(--bg-element);
  height: 36px;
  gap: 8px;
}

.SeparatorRoot {
  background-color: var(--line);
}

.SeparatorRoot[data-orientation='horizontal'] {
  height: 1px;
  width: 100%;
}
.SeparatorRoot[data-orientation='vertical'] {
  height: 100%;
  width: 1px;
}
</style>
